import { Api } from "./base/api";
import { settings } from "../utils/constants";
export class WebLarekApi extends Api {
    constructor(baseUrl, options) {
        super(baseUrl, options);
    }
    getProductList() {
        return this.get(settings.request_productList);
    }
    getProduct(id) {
        return this.get(`${settings.request_productItem}${id}`);
    }
    postOrder(data) {
        return this.post(settings.request_order, data);
    }
}
